//
import Vue from 'vue'
import VueRouter from 'vue-router'

//Components
import Home from '../components/Home'
import Otp from '../components/otp'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        component: Home,
        name: 'home'
    },
    {
        path: '/login',
        component: Otp,
        name: 'Login'
    }
]

export default new VueRouter({
    routes,
    mode: 'history'
})